import styles from "./articles.scss"
import mainStyles from "./index.scss"
import Page from "../components/page"
import Nav from "../components/nav"

const Articles = () => {
    return <Page>
        <div className={styles.anibar}></div>
        <header className={styles.header}>
            <a href="#top" className={styles.avatar} alt="Systemcluster"></a>
            <h1 className={styles.title}>
                <span>
                <span>Systemcluster</span>
                </span>
            </h1>
        </header>
        <main className={mainStyles.main}>
            <Nav />
            <section className={mainStyles.content}>
                <header className={styles.bar}>
                    <div className={styles.barbg}></div>
                    <h2>Articles</h2>
                </header>
                <article>
                </article>
            </section>
        </main>
    </Page>
}

export default Articles;
